<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">
    <div class="task pageSpace h100">
        <div>
            <a-form :label-width="80">
                <a-row :gutter="[24,24]" style="margin:0">
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">开始时间</label>
                        <a-date-picker class="searchContentCss" format="YYYY-MM-DD HH:mm:ss"
                                       :show-time="{defaultValue:moment('00:00:00','HH:mm:ss')}"
                                       v-model="beginTime"></a-date-picker>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">结束时间</label>
                        <a-date-picker class="searchContentCss" format="YYYY-MM-DD HH:mm:ss"
                                       :show-time="{defaultValue:moment('00:00:00','HH:mm:ss')}"
                                       v-model="endTime"></a-date-picker>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">协议</label>
                        <a-select class="searchContentCss" v-model="agrementValue.value" mode="multiple" filterable
                                  remote :max-tag-count="1">
                            <a-select-option value="mta">SMTP</a-select-option>
                            <a-select-option value="pop3">POP3</a-select-option>
                            <a-select-option value="imap">IMAP</a-select-option>
                            <a-select-option value="wms">HTTP</a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">单位</label>
                        <a-select class="searchContentCss" v-model="unitValue.value" :allowClear="true">
                            <a-select-option v-for="item in units" :key="item.ename" :value="item.ename">{{item.cname}}</a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">分析对象</label>
                        <a-select class="searchContentCss" v-model="analysisObj.value" :allowClear="true">
                            <a-select-option v-for="item in analysisObjList" :key="item">{{item}}</a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <label style="color: white;" class="searchCss">时间偏移量</label>
                        <a-select class="searchContentCss" v-model="timeOffSetValue.value" mode="multiple" filterable
                                  remote :max-tag-count="1">
                            <a-select-option value="tenM">10分钟</a-select-option>
                            <a-select-option value="threeM">30分钟</a-select-option>
                            <a-select-option value="oneH">1小时</a-select-option>
                            <a-select-option value="oenD">1天</a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <a-select style="width: 15%;margin-left: 48px" v-model="CityValues.value">
                            <a-select-option value="yes">包含</a-select-option>
                            <a-select-option value="no">不包含</a-select-option>
                        </a-select>
                        <a-select style="width: 60%;" v-model="nationValue.value" mode="multiple"
                                  filterable remote :max-tag-count="1">
                            <a-select-option value="all">全部</a-select-option>
                        </a-select>
                    </a-col>
                    <a-col :xxl="8" :xl="12" style="padding: 0 12px 12px;">
                        <a-select style="width: 15%;margin-left: 48px" v-model="CityValues.value">
                            <a-select-option value="yes">包含</a-select-option>
                            <a-select-option value="no">不包含</a-select-option>
                        </a-select>
                        <a-select style="width: 60%;" v-model="nationValue.value" mode="multiple"
                                  filterable remote :max-tag-count="1">
                            <a-select-option value="all">全部</a-select-option>
                        </a-select>
                    </a-col>
                </a-row>
                <a-row>
                    <a-col :lg="2" style="float: right">
                        <a-button type="primary" @click="exportCsv" :loading="isLoading">
                            <a-icon type="export"></a-icon>
                            导出
                        </a-button>
                    </a-col>
                    <a-col :lg="2" style="float: right">
                        <a-button type="primary" @click="searchData">
                            <a-icon type="search"></a-icon>
                            查询
                        </a-button>
                    </a-col>
                </a-row>

            </a-form>
        </div>
        <div style="margin-top: 40px">
            <a-table :columns="columns" :data-source="tableData" rowKey="id" :loading="loading"
                     :pagination="{showSizeChanger:true,showQuickJumper:true,pageSizeOptions:sizeList}"> 
                     <!-- @click="emailNameClick(record)" style="cursor: pointer;color: #2D8CF0" -->
                  <span slot="emailName" slot-scope="text,record">
                    <span>{{record.emailName}}</span>
                </span>
                <!--<span slot="action" slot-scope="text,record">
                    <span class="t-optColor-success" @click="detailsChange(record)" style="cursor: pointer">详情</span>
                </span>-->
            </a-table>
            <p style="float: right;margin-left: 64px;margin-top: 16px; font-size: 15px;">总条数：{{total}}</p>
            <!--  <div style="margin-top: 20px;text-align: right">
                  <a-pagination v-model="page" :pageSize="pageSize" :total="total" @change="pageChange" @showSizeChange="sizeChange"></a-pagination>
              </div>-->
        </div>
        <!-- --详情模态框--- -->
        <a-modal :title="detailsTitle" v-model="modalVisible" style="width: 80%" class="modalCls">
            <a-table border :columns="deColumns" :dataSource="detailsData" rowKey="id" :loading="detailLoading"
                     :pagination="pagination"></a-table>
            <p>
             <span>
               当前显示:
               <span style="font-size:20px;color:#2D8CF0">{{detailsData.length}}</span>
              </span>
            </p>
            <div slot="footer">
                <a-button type="primary" v-if="detailsData.length < detailTotal"
                          :disabled='detailLoading'>
                    加载更多
                </a-button>
                <span v-else>已显示全部</span>
            </div>
        </a-modal>
    </div>

</template>

<script>
    import moment from 'moment';

    export default {
        name: "distanceAnalyze",
        data() {
            return {
                pagination: {},
                detailTotal: 0,
                value1: null,
                loading: false,
                detailsTitle: '详情记录',
                modalVisible: false,
                total: 0,
                sizeList: ["5", "10", "20", "30"],
                isLoading:false,  // 导出加载
                columns: [
                    {
                        dataIndex: "emailName",
                        title: "邮箱名",
                        ellipsis: true,
                        scopedSlots: {customRender: 'emailName'}
                    },
                    {
                        dataIndex: "countryCount",
                        title: "境外IP",
                    },
                    {
                        dataIndex: "ipcount",
                        title: "境外归属地",
                    },
                    {
                        dataIndex: "ipcount",
                        title: "时间",
                    },
                    {
                        dataIndex: "ipcount",
                        title: "境内IP",
                    },
                    {
                        dataIndex: "ipcount",
                        title: "境内归属地",
                    },
                ],
                deColumns: [
                    {
                        title: "邮箱名",
                        dataIndex: "emailName",
                    },
                    {
                        title: "成功个数",
                        dataIndex: "successCount"
                    },
                    {
                        title: "失败个数",
                        dataIndex: "failCount"
                    }

                ],
                detailsData: [],
                tableData: [],
                detailLoading: false,  // 模态框表格加载
                beginTime: '', //开始时间
                endTime: '',  // 结束时间
                country: '2',
                city: '2',
                // 协议类型
                agrementValue: {
                    label: "ptTag",
                    value: []
                },
                // 时间偏移量
                timeOffSetValue: {
                    label: "timeOffSet",
                    value: []
                },
                // 单位
                unitValue: {
                    label: "unit",
                    value: []
                },
                // 分析对象
                analysisObj: {
                    label: 'analysis',
                    value: ''
                },
                analysisObjList: [],  // 分析对象数据
                units: [],  // 单位
                unitName: '',
                // 是否包含
                CityValues: {
                    label: "orNot",
                    value: "yes"
                },
                nationValue: {
                    label: "geoip.country_name",
                    value: "all"
                },
            }
        },
        methods: {
            moment,
            onChange(date, dateString) {
                console.log(date, dateString)
            },
            // 查看详情
            detailsChange(row) {
                console.log(row)
            },
            // 跳转到详情
            emailNameClick(val) {
                console.log(val)
            },
            searchData() {
            },
            // 导出
            exportCsv() {
            },
            // 获取例表数据
            getDistanceList() {
            },

        },
        created() {
        }

    };
</script>

<style scoped>
    /*.searchCss {
      text-align: right;
    }*/

    .alignLeft {
        text-align: left;
    }

    .alignRight {
        text-align: right;
        padding: 3px;
        margin-top: 5px;
        color: #ffffff;
    }

    .inline-block {
        display: inline-block;
    }

    .col-style {
        margin: 7px 0;
        height: 32px;
    }
</style>
